import * as yup from "yup";

export const schemaSingUp = yup
  .object({
    username: yup.string().required("  '  "),
    email: yup.string().required("  '  "),
    password: yup.string().required("  '  "),
    confirmPassword: yup
      .string()
      .required("  '  ")
      .oneOf([yup.ref("password"), null], "   "),
  })
  .required();
