import * as yup from "yup";

export const schemaAlbumNew = yup
  .object({
    name_album: yup.string().required("  '  "),
    group_name: yup.string().required("  '  "),
    release_date: yup.string().required("  '  "),
    genre: yup.array().required("  '  "),
    // picture: yup.object().required("  '  "),
  })
  .required();

