import * as yup from "yup";

export const schemaTrackNew = yup
  .object({
    name: yup.string().required("  '  "),
    author_track: yup.string().required("  '  "),
    date_implementation: yup
      .string()
      .required("  '  "),
    album: yup.string().required("  '  "),
    text_track: yup.string().required("  '  "),
    genre: yup.array().required("  '  "),

    // picture: yup.object().required("  '  "),
    // audio: yup.object().required("  '  "),
  })
  .required();

